.overlay {
  position: fixed;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
  z-index: 14;
  opacity: 1;
}
.overlay-open .sidebar {
  margin-left: 0;
  z-index: 27;
  top: 0;
  height: 100vh;
}
.overlay-open .sidebar .admin-action-info span {
  color: #999 !important;
}
.overlay-open .sidebar .admin-action-info h3 {
  color: #ccc !important;
}
.overlay-open .sidebar .admin-action-info ul li a {
  color: #999 !important;
}
.overlay-open .sidebar .quick-stats h5 {
  color: #666 !important;
}
.sidebar {
  transition: all 0.5s ease-in-out;
  font-family: 'Roboto', sans-serif;
  
  
  overflow: hidden;
  display: inline-block;
  height: calc(100vh - 0px);
  position: fixed;
  top: 70px;
  left: 0;
  z-index: 11;
}
.sidebar .legal {
  position: absolute;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #eee;
  padding: 15px;
  overflow: hidden;
  background: #edf1f5;
}
.sidebar .legal .version {
  white-space: nowrap;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-top: 5px;
}
.sidebar .user-info {
  padding: 13px 15px 12px 15px;
  white-space: nowrap;
  position: relative;
  height: 184px;
}
.sidebar .user-info .admin-image {
  display: table-cell;
  vertical-align: top;
}
.sidebar .user-info .admin-image > img {
  border-radius: 50%;
  float: left;
  width: 72px;
  height: auto;
}
.sidebar .user-info .admin-action-info {
  display: table-cell;
  padding-left: 20px;
  vertical-align: middle;
}
.sidebar .user-info .admin-action-info > span {
  color: #fff;
  float: left;
}
.sidebar .user-info .admin-action-info h3 {
  color: #fff;
  float: left;
  font-weight: 700;
  line-height: 22px;
  margin: 0;
  width: 100%;
}
.sidebar .user-info .admin-action-info > ul {
  float: left;
  list-style: outside none none;
  margin: 5px 0 0 -5px;
  padding: 0;
  width: 100%;
}
.sidebar .user-info .admin-action-info > ul li {
  float: left;
  margin-right: 1px;
}
.sidebar .user-info .admin-action-info > ul li a {
  color: #fff;
  float: left;
  height: 29px;
  line-height: 29px;
  text-align: center;
  width: 29px;
}
.sidebar .user-info .quick-stats {
  float: left;
  text-align: left;
  width: 100%;
}
.sidebar .user-info .quick-stats > h5 {
  color: #fff;
  float: left;
  font-weight: 400;
  letter-spacing: 0.3px;
  line-height: 22px;
  margin: 5px 0;
  width: 100%;
  text-transform: uppercase;
}
.sidebar .user-info .quick-stats > ul {
  list-style: outside none none;
  padding: 0;
}
.sidebar .user-info .quick-stats > ul > li {
  float: left;
  padding: 0 1px;
  width: 33.333%;
}
.sidebar .user-info .quick-stats > ul > li > span {
  background: #ffffff none repeat scroll 0 0;
  border-radius: 2px;
  color: #424242;
  float: left;
  padding: 5px 0;
  text-align: center;
  width: 100%;
  font-weight: 700;
}
.sidebar .user-info .quick-stats > ul > li > span i {
  color: #757575;
  float: left;
  font-style: normal;
  letter-spacing: 0.3px;
  line-height: 14px;
  width: 100%;
  font-weight: 100;
}
.sidebar .menu {
  position: relative;
  overflow-y: auto;
  background: #30373e;
}
.sidebar .menu .list {
  list-style: none;
  padding-left: 0;
}
.sidebar .menu .list li.active > :first-child span {
  font-weight: 700;
}
.sidebar .menu .list .header {
  background: #363e46;
  padding: 8px 16px;
  color: #ccc;
}
.sidebar .menu .list i.material-icons {
  margin-top: 4px;
}
.sidebar .menu .list .menu-toggle:after,
.sidebar .menu .list .menu-toggle:before {
  position: absolute;
  top: calc(50% - 14px);
  right: 17px;
  font-size: 19px;
  -webkit-transform: scale(0);
  transform: scale(0);
  transition: all 0.3s ease-in-out;
  color: #888;
}
.sidebar .menu .list .menu-toggle:before {
  content: '+';
  -webkit-transform: scale(1);
  transform: scale(1);
}
.sidebar .menu .list .menu-toggle:after {
  content: '\2013';
  -webkit-transform: scale(0);
  transform: scale(0);
}
.sidebar .menu .list .menu-toggle.toggled:before {
  -webkit-transform: scale(0);
  transform: scale(0);
}
.sidebar .menu .list .menu-toggle.toggled:after {
  -webkit-transform: scale(1);
  transform: scale(1);
}
.sidebar .menu .list a {
  color: #9e9e9e;
  position: relative;
  display: inline-flex;
  vertical-align: middle;
  width: 100%;
  padding: 13px;
  font-weight: 400;
}
.sidebar .menu .list a:hover,
.sidebar .menu .list a:active,
.sidebar .menu .list a:focus {
  text-decoration: none !important;
}
.sidebar .menu .list a small {
  position: absolute;
  top: calc(50% - 7.5px);
  right: 15px;
}
.sidebar .menu .list a span {
  margin: 0;
  font-weight: 400;
  overflow: hidden;
  padding-left: 15px;
}
.sidebar .menu .list a i {
  margin-top: 3px;
  width: 20px;
  text-align: center;
}
.sidebar .menu .list .ml-menu {
  list-style: none;
  display: none;
  padding-left: 0;
}
.sidebar .menu .list .ml-menu span {
  font-weight: 400;
  margin: 3px 0 1px 6px;
}
.sidebar .menu .list .ml-menu li a {
  padding-left: 48px;
  padding-top: 7px;
  padding-bottom: 7px;
}
.sidebar .menu .list .ml-menu li a:before {
  content: '\f2fb';
  position: absolute;
  left: 21px;
  font-family: 'Material-Design-Iconic-Font';
  color: #999;
}
.sidebar .menu .list .ml-menu li.active a.toggled:not(.menu-toggle) {
  font-weight: 500;
  margin-left: 5px;
}
.sidebar .menu .list .ml-menu li.active a.toggled:not(.menu-toggle):before {
  content: '\E315';
  font-family: 'Material Icons';
  position: relative;
  font-size: 21px;
  height: 20px;
  top: -5px;
  right: 0px;
}
.sidebar .menu .list .ml-menu li .ml-menu li a {
  padding-left: 80px;
}
.sidebar .menu .list .ml-menu li .ml-menu .ml-menu li a {
  padding-left: 95px;
}
.sidebar .active.open .active a {
  color: #ccc;
  margin: 0 !important;
}
.sidebar .active.open .active a:before {
  display: none;
}
.sidebar .active.open .active a:after {
  content: '\f2fb';
  position: absolute;
